<template>
  <div class="wraper-home">
    <mt-header title="小学语文">
      <router-link to="/" slot="left">
        <mt-button icon="back">返回</mt-button>
      </router-link>
    </mt-header>
    <div class="page-content">
        <div class="page-start" v-if="isFirst" @click="start">开始</div>
        <div class="page-main" v-else :class="{'page-info':isShowInfo}">
            <div class="page-text">
                <div class="py">{{showItem ? showItem.py : ''}}</div>
                <div class="mzg" >{{text}}</div>
            </div>
            <div class="page-zc-list" :class="{'active':isShowInfo}">
                <div class="page-zc clearfix" v-for="(zc,i) in showItem.zc" :key="i">
                    <div class="mzg" v-for="(cy, k) in zc.split('')" :key="k">{{cy}}</div>
                </div>
            </div>
            <div class="page-btns">
                <el-button size="medium" type="primary" @click="start">下一个</el-button>
                <el-button size="medium" type="success" @click="isShowInfo= true">提示</el-button>
            </div>
        </div>
    </div>
  </div>
</template>
<script>
import hanzi from '../../../data/hanZi.json'
export default {
  name: 'suiji',
  data() {
    return {
        // 是否第一次进入页面
        isFirst: true,
        // 是否显示文字动画
        isAni: false,
        // 动画时间
        aniTimer: null,
        aniCountTime: 0,
        // 显示文字
        text: '',
        // 记录已显示过的文字id
        idLog:[],
        showItem: {
            zc: [],
            py: '',
        },
        isShowInfo: false,
    };
  },
  watch: {
      isAni(val){
          if(val){
              this.startTimer();
          }
      }
  },
  computed:{
      // 可以显示的数据
      showData(){
         return hanzi.data.filter((dt) => this.idLog.indexOf(dt.id) < 0)
      }
  },
  methods: {
      start(){
          this.isFirst = false;
          this.isAni = true;
          this.isShowInfo = false;
      },
      startTimer(){
        clearTimeout(this.aniTimer)
        this.aniTimer = setTimeout(()=>{
            const obj = this.getRandomData()
            this.text = obj.name;
            if(this.aniCountTime < 10){
                this.startTimer()
                this.aniCountTime += 1;
            }else{
                 this.aniCountTime = 0;
                 this.isAni = false;
                 this.idLog.push(obj.id)
                 this.showItem = obj
            }
        },100)
      },
      // 获取随机数据
      getRandomData(){
          const len = this.showData.length;
          if(len===0) return '';
          return this.showData[Math.floor(Math.random() * len)]
      },
  },
  mounted() {
      console.log('--', hanzi);
  }
};
</script>
<style lang="scss" scoped>
$width: 100px;

.wraper-home {
  width: 100%;
  height: 100%;
  position: relative;
  font-size: 3rem;
  .page-content {
      width: 100%;
      position: absolute;
      top: 40px;
      bottom: 0;
      .page-start{
          width: 200px;
          height: 200px;
          border-radius: 100%;
          background:#67c23a;
          color: #fff;
          text-align: center;
          line-height: 200px;
          position: absolute;
          top:50%;
          left: 50%;
          margin-left: -100px;
          margin-top: -100px;
      }
      .page-main{
          width: 100%;
          height: 100%;
          position: relative;
          .page-text{
              width: $width;
              position: absolute;
              top: 20%;
              left: 40%;
              text-align: center;
              .mzg{
                 font-size: 4rem;
              }
              .py{
                  opacity: 0;
              }

          }
        
        .page-btns{
            position: absolute;
            top: 90%;
            width: 100%;
            text-align: center;
        }
      }
      .page-main.page-info {
          .page-text{
              left: 5%;
              margin-left: 0;
              transition:left 2s;
            .py{
                opacity: 1;
                animation: fadein 2s;
            }
          }
      }
    }
}
.py{
    width: $width;
    height: $width;
    line-height: 90px;
    background: url(../../../assets/img/py.png) no-repeat;
    background-size: cover;
    text-align: center;
}
.mzg{
    width: $width;
    height: $width;
    line-height: $width;
    text-align: center;
    background: url(../../../assets/img/mzg.png) no-repeat;
    background-size: cover;
}
.page-zc-list{
    position: absolute;
    right: 5%;
    height: 400px;
    top: 45%;
    margin-top: -200px;
    opacity: 0;
    .page-zc{
        margin-bottom: 20px;
        .mzg{float: left;margin-left: 10px;}
    }
    &.active{
        opacity: 1;
        transition: opacity 2s 2s;
    }
}
@keyframes fadein{
    0%{opacity:0;}
    100%{opacity:1;}
}
@-webkit-keyframes fadein{/*针对webkit内核*/  
    0%{opacity:0;}
    100%{opacity:1;}
}  
</style>
